import { getData, STATIC_MEDIA_URL } from '../api/max';

export const getResponsiveImageProps = (
  imageProps: Record<string, any>,
  targetWidth: number,
  targetHeight: number,
) => {
  const {
    displayMode,
    uri,
    width,
    height,
    name,
    crop,
    focalPoint,
    alignType,
    quality,
    upscaleMethod,
    hasAnimation,
    allowAnimatedTransform,
  } = imageProps;

  const { srcset, css } = getData(
    displayMode,
    { id: uri as string, width, height, name, crop, focalPoint },
    {
      width: targetWidth as number,
      height: targetHeight as number,
      alignment: alignType,
    },
    {
      focalPoint,
      name,
      quality: quality?.quality,
      upscaleMethod,
      hasAnimation,
      allowAnimatedTransform,
      useSrcset: true,
    },
  );

  const srcsetString =
    srcset?.dpr
      ?.map((s: string) => (/^[a-z]+:/.test(s) ? s : `${STATIC_MEDIA_URL}${s}`))
      .join(', ') || '';

  return {
    srcset: srcsetString,
    css,
  };
};
